.App {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

table {
  border: 2px solid crimson;
  width: 800px;
  height: 200px;
}

th {
  border-bottom: 2px solid crimson;
}

td {
  text-align: center;
}
label {
  color: blue;
  font-size: 25px;
  text-shadow: 3px 3px 5px orangered;
}
.buttonHolder {
  text-align: center;
  background-color: #008cba;
  color: blue;
  border: 2px solid #4caf50;
}
.buttonHolder1 {
  text-align: center;
  background-color: red;
  color: blue;
  border: 4px solid gold;
}
.timer {
  text-align: center;
  background-color: navy;
  color: yellow;
  border: 4px solid green;
}
